import React, {useState} from 'react';
import {OptionWrapper,Result,OptionInfoDescription,OptionInfoHeading,OptionInfo,ResultNumber,OptionHeading,NumberInput,FormWrapper} from '../../Styles/OptionStyles';

const Option8 = () => {
     const [number1, setNumber1] = useState(0);
     const [number2, setNumber2] = useState(1);
     
     const formula = (num1,num2) => {
           return Number(num1)/Number(num2);
     }

     return (
          <OptionWrapper>
               <OptionHeading>   EBITDA</OptionHeading>
               <h3>    EBITDA</h3>
               <FormWrapper>
               <NumberInput 
                         type="number" 
                         name="number1"
                         placeholder=' '
                         onChange={(e) => setNumber1(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number2"
                         placeholder='EBITDA'
                         onChange={(e) => setNumber2(e.target.value)}
                    />
               </FormWrapper>
               <Result>   EBITDA: <ResultNumber>{formula(number1,number2)}</ResultNumber></Result>
               <div>
                    <OptionInfo>
                         <OptionInfoHeading></OptionInfoHeading>
                         <OptionInfoDescription>
                             EBITDA (Debt/EBITDA ratio)       ,      ().     ,      ,      EBITDA     ,   . ,      EBITDA -      (          ).
                         <br/><br/>
                             EBITDA       ,   ,          .
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading> ()</OptionInfoHeading>
                         <OptionInfoDescription>
                             :
                         <br/><br/>
                           / EBITDA
                         <br/><br/>
                             ,    ,          .
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading> </OptionInfoHeading>
                         <OptionInfoDescription>    EBITDA      ,      ().     ,       3.     4-5,                .            .
                         <br/><br/>
                             ,     EBITDA    ,            .    ,    ,    ,     ,       , ..        ,       EBITDA.          ,       .        , , ,   ,          EBITDA.</OptionInfoDescription>
                    </OptionInfo>
               </div>
          </OptionWrapper>
     )
}

export default Option8;