import React, {useState} from 'react';
import {OptionWrapper,Result,OptionInfoDescription,OptionInfoHeading,OptionInfo,ResultNumber,OptionHeading,NumberInput,FormWrapper} from '../../Styles/OptionStyles';

const Option5 = () => {
     const [number1, setNumber1] = useState(0);
     const [number2, setNumber2] = useState(0);
     const [number3, setNumber3] = useState(1);
     
     const formula = (num1,num2,num3) => {
           return (Number(num1)+Number(num2))/Number(num3);
     }

     return (
          <OptionWrapper>
               <OptionHeading>  </OptionHeading>
               <h3>   </h3>
               <FormWrapper>
               <NumberInput 
                         type="number" 
                         name="number1"
                         placeholder=' '
                         onChange={(e) => setNumber1(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number2"
                         placeholder=' '
                         onChange={(e) => setNumber2(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number3"
                         placeholder=' '
                         onChange={(e) => setNumber3(e.target.value)}
                    />
               </FormWrapper>
               <Result>  : <ResultNumber>{formula(number1,number2,number3)}</ResultNumber></Result>
               <div>
                    <OptionInfo>
                         <OptionInfoHeading></OptionInfoHeading>
                         <OptionInfoDescription>
                               , ,         :     .        .
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading> ()</OptionInfoHeading>
                         <OptionInfoDescription>
                            = ( +F1[1400])/F1[1600],
                         <br/><br/>
                         
                         <br/>
                           =    "  "        ;
                         <br/>
                         F1[1400]    "  ";
                         <br/>
                         F1[1600]    (..    ).
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading> </OptionInfoHeading>
                         <OptionInfoDescription>    1  ,                   .    0,7-0,8  ,           ,                (,   ..),     .               .</OptionInfoDescription>
                    </OptionInfo>
               </div>
          </OptionWrapper>
     )
}

export default Option5;