import React, {useState} from 'react';
import {OptionWrapper,Result,OptionInfoDescription,OptionInfoHeading,OptionInfo,ResultNumber,OptionHeading,NumberInput,FormWrapper} from '../../Styles/OptionStyles';

const Option2 = () => {
     const [number1, setNumber1] = useState(0);
     const [number2, setNumber2] = useState(0);
     
     const formula = (num1,num2) => {
           return Number(num1)+Number(num2);
     }

     return (
          <OptionWrapper>
               <OptionHeading>EBITDA</OptionHeading>
               <h3> EBITDA</h3>
               <FormWrapper>
                    <NumberInput 
                         type="number" 
                         name="number1"
                         placeholder=' ()  '
                         onChange={(e) => setNumber1(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number2"
                         placeholder='  '
                         onChange={(e) => setNumber2(e.target.value)}
                    />
               </FormWrapper>
               <Result>EBITDA:  <ResultNumber>{formula(number1,number2)}</ResultNumber></Result>
               <div>
                    <OptionInfo>
                         <OptionInfoHeading></OptionInfoHeading>
                         <OptionInfoDescription>EBITDA (Earnings Before Interest, Taxes, Depreciation and Amortization)     ,   . EBITDA    ,      (.. ,    ),      . EBITDA     ,   ""    .       ,     .
                         <br/><br/>
                         EBITDA,   ,         (EV/EBITDA,    .).     EBITDA      .
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading> ()</OptionInfoHeading>
                         <OptionInfoDescription>EBITDA =  ()   + (   +      )
                         <br/><br/>
                          ,       .     EBITDA     "    " ("   "),          ,       .
                         <br/><br/>
                          EBITDA   ,        ,      , , .
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading>  EBITDA</OptionInfoHeading>
                         <OptionInfoDescription>
                          EBITDA      (EBITDA margin): <br/>
                         EBITDA margin = EBITDA /   
                         <br/><br/>
                                ,   ,  ,     EBITDA,  ,   .
                         </OptionInfoDescription>
                    </OptionInfo>
               </div>
          </OptionWrapper>
     )
}

export default Option2;