import React, {useState} from 'react';
import {OptionWrapper,Result,OptionInfoDescription,OptionInfoHeading,OptionInfo,ResultNumber,OptionHeading,NumberInput,FormWrapper} from '../../Styles/OptionStyles';

const Option5 = () => {
     const [number1, setNumber1] = useState(0);
     const [number2, setNumber2] = useState(0);
     const [number2_1, setNumber2_1] = useState(0);
     const [number2_2, setNumber2_2] = useState(0);
     const [number2_3, setNumber2_3] = useState(0);
     
     const formula = (num1,num2) => {
          return Number(num1)-Number(num2);
    }

    const formula2 = (num1,num2,num3) => {
          return (Number(num1)+Number(num2))-Number(num3);
     }    

     return (
          <OptionWrapper>
               <OptionHeading> </OptionHeading>
               <h3>    <u></u> </h3>
               <FormWrapper>
                    <NumberInput 
                         type="number" 
                         name="number1"
                         placeholder=' '
                         onChange={(e) => setNumber1(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number2"
                         placeholder=' '
                         onChange={(e) => setNumber2(e.target.value)}
                    />
               </FormWrapper>
               <Result> : <ResultNumber>{formula(number1,number2)}</ResultNumber></Result>
               <br/><br/>
               <h2></h2>
               <br/><br/>
               <h3>    <u></u> </h3>
               <FormWrapper>
                    <NumberInput 
                         type="number" 
                         name="number2_1"
                         placeholder=' '
                         onChange={(e) => setNumber2_1(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number2_2"
                         placeholder=' '
                         onChange={(e) => setNumber2_2(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number2_3"
                         placeholder=' '
                         onChange={(e) => setNumber2_3(e.target.value)}
                    />
               </FormWrapper>
               <Result> : <ResultNumber>{formula2(number2_1,number2_2,number2_3)}</ResultNumber></Result>

               <div>
                    <OptionInfo>
                         <OptionInfoHeading></OptionInfoHeading>
                         <OptionInfoDescription>         " working capital" (   "  ").        "  ".     ,          (..        ).</OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading> ()</OptionInfoHeading>
                         <OptionInfoDescription>
                             :
                           =     
                         <br/><br/>
                               :
                         <br/><br/>
                           = (  +  ) -  
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading>  </OptionInfoHeading>
                         <OptionInfoDescription>
                                  ,    .       , ,   ,       ,   -     ,  ,      .
                         </OptionInfoDescription>
                    </OptionInfo>
               </div>
          </OptionWrapper>
     )
}

export default Option5;