import React, {useState} from 'react';
import {OptionWrapper,LabelForInput,Result,OptionInfoDescription,OptionInfoHeading,OptionInfo,ResultNumber,OptionHeading,NumberInput,FormWrapper} from '../../Styles/OptionStyles';

const Option4 = () => {
     const [number1, setNumber1] = useState(0);
     const [number2, setNumber2] = useState(0);
     const [number3, setNumber3] = useState(0);
     const [number4, setNumber4] = useState(0);
     const [number5, setNumber5] = useState(0);
     
     const formula = (num1,num2,num3,num4,num5) => {
           const result = 2*Number(num1)+0.1*Number(num2)+0.08*Number(num3)+0.45*Number(num4)+Number(num5);
          if(isNaN(result)||result===Infinity||result===-Infinity){
               return 0;
          }else{
               return result;
          }
     }

     return (
          <OptionWrapper>
               <OptionHeading> -</OptionHeading>
               <h3>   -</h3>
               <FormWrapper>
                    <NumberInput 
                         type="number" 
                         id="number1"
                         placeholder='1'
                         onChange={(e) => setNumber1(e.target.value)}
                    />
                    <LabelForInput htmlFor="number1">   </LabelForInput>
                    <NumberInput 
                         type="number" 
                         id="number2"
                         placeholder='2'
                         onChange={(e) => setNumber2(e.target.value)}
                    />
                    <LabelForInput htmlFor="number2">  </LabelForInput>
                    <NumberInput 
                         type="number" 
                         id="number3"
                         placeholder='3'
                         onChange={(e) => setNumber3(e.target.value)}
                    />
                    <LabelForInput htmlFor="number3">  </LabelForInput>
                    <NumberInput 
                         type="number" 
                         id="number4"
                         placeholder='4'
                         onChange={(e) => setNumber4(e.target.value)}
                    />
                    <LabelForInput htmlFor="number4"> </LabelForInput>
                    <NumberInput 
                         type="number" 
                         id="number5"
                         placeholder='5'
                         onChange={(e) => setNumber5(e.target.value)}
                    />
                    <LabelForInput htmlFor="number5">  </LabelForInput>
               </FormWrapper>
               <Result> -: <ResultNumber>{formula(number1,number2,number3,number4,number5)}</ResultNumber></Result>
               
               <div>
                    <OptionInfo>
                         <OptionInfoHeading></OptionInfoHeading>
                         <OptionInfoDescription>      ,           3    .
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading> ()</OptionInfoHeading>
                         <OptionInfoDescription>                ,         ()  12.08.1994 . N 31-).  ,    :
                         <br/><br/>
                            = (1 + 3/ (1 - 1)) / 1
                         <br/><br/>
                         
                         <br/><br/>
                         1 -   (   )    (1);<br/>
                         1 -       ;<br/>
                         1 -     ; 1 = 2;<br/>
                         3 -      ;<br/>
                          -    .
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading> </OptionInfoHeading>
                         <OptionInfoDescription>     1,          ,   . ..            3       2,  .
                         <br/><br/>
                           ,   ,     ,     .    ,                    .          4   .</OptionInfoDescription>
                    </OptionInfo>
               </div>
          </OptionWrapper>
     )
}

export default Option4;