import React, {useState} from 'react';
import {OptionWrapper,Result,OptionInfoDescription,OptionInfoHeading,OptionInfo,ResultNumber,OptionHeading,NumberInput,FormWrapper} from '../../Styles/OptionStyles';

const Option1 = () => {
     const [number1, setNumber1] = useState(0);
     const [number2, setNumber2] = useState(0);
     
     const formula = (num1,num2) => {
           return Number(num1)+Number(num2);
     }

     return (
          <OptionWrapper>
               <OptionHeading>EBIT </OptionHeading>
               <h3> EBIT</h3>
               <FormWrapper>
                    <NumberInput 
                         type="number" 
                         name="number1"
                         placeholder=' ()  '
                         onChange={(e) => setNumber1(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number2"
                         placeholder='  '
                         onChange={(e) => setNumber2(e.target.value)}
                    />
               </FormWrapper>
               <Result>EBIT:  <ResultNumber>{formula(number1,number2)}</ResultNumber></Result>
               <div>
                    <OptionInfo>
                         <OptionInfoHeading></OptionInfoHeading>
                         <OptionInfoDescription>EBIT (Earnings Before Interest and Taxes)       .       ,     .           (  )   ,        .  EBIT    ,    EBIT        .
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading> ()</OptionInfoHeading>
                         <OptionInfoDescription> EBIT    "    "     ()          :
                         <br/><br/>
                         EBIT = .2300 " ()  " + .2330 "  "
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading> </OptionInfoHeading>
                         <OptionInfoDescription>      EBIT.            (,      )   .
                         <br/><br/>
                                EBITDA  ,   EBIT        .      ,  ""      ,  EBITDA      .          EBITDA,   EBIT.</OptionInfoDescription>
                    </OptionInfo>
               </div>
          </OptionWrapper>
     )
}

export default Option1;