import React, {useState} from 'react';
import {OptionWrapper,Result,OptionInfoDescription,OptionInfoHeading,OptionInfo,ResultNumber,OptionHeading,NumberInput,FormWrapper} from '../../Styles/OptionStyles';

const Option5 = () => {
     const [number1, setNumber1] = useState(0);
     const [number2, setNumber2] = useState(0);
     const [number3, setNumber3] = useState(0);
     const [number4, setNumber4] = useState(1);
     
     const formula = (num1,num2,num3,num4) => {
          const result = (Number(num1)+(Number(num2)-Number(num3)))/Number(num4);
          if(isNaN(result)||result===Infinity||result===-Infinity){
               return 0;
          }else{
               return result;
          }
     }
     const formula2 = (num1,num2,num3,num4) => {
          const result = 356/((Number(num1)+(Number(num2)-Number(num3)))/Number(num4));
          if(isNaN(result)||result===Infinity||result===-Infinity){
               return 0;
          }else{
               return result;
          }
     }

     
     return (
          <OptionWrapper>
               <OptionHeading>  </OptionHeading>
               <h3>   </h3>
               <FormWrapper>
                    <NumberInput 
                         type="number" 
                         name="number1"
                         placeholder=' '
                         onChange={(e) => setNumber1(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number2"
                         placeholder='   '
                         onChange={(e) => setNumber2(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number3"
                         placeholder='   '
                         onChange={(e) => setNumber3(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number4"
                         placeholder='   '
                         onChange={(e) => setNumber4(e.target.value)}
                    />
               </FormWrapper>
               <Result>   : <ResultNumber>{formula(number1,number2,number3,number4)}</ResultNumber></Result>
               <Result>    : <ResultNumber>{formula2(number1,number2,number3,number4)}</ResultNumber></Result>
               <div>
                    <OptionInfo>
                         <OptionInfoHeading></OptionInfoHeading>
                         <OptionInfoDescription>   (accounts payable turnover ratio)            .   ,   (,  )       .
                         <br/><br/>
                             ,         ,  .
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading> ()</OptionInfoHeading>
                         <OptionInfoDescription>                (  ,       ).
                         <br/><br/>
                            () =  /    
                         <br/><br/>
                                ,    :
                         <br/><br/>
                          =   + (        )
                         <br/><br/>
                                 ,       .
                            ,          . ..      (       ),        .
                            (" "),     :
                         <br/><br/>
                              = 365 /    
                         <br/><br/>
                              ,       .
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading> </OptionInfoHeading>
                         <OptionInfoDescription>      ,   .       ,         ,              .</OptionInfoDescription>
                    </OptionInfo>
               </div>
          </OptionWrapper>
     )
}

export default Option5;