import React, {useState} from 'react';
import {OptionWrapper,Result,LabelForInput,OptionInfoDescription,OptionInfoHeading,OptionInfo,ResultNumber,OptionHeading,NumberInput,FormWrapper} from '../../Styles/OptionStyles';

const Option3 = () => {
     const [number1, setNumber1] = useState(0);
     const [number2, setNumber2] = useState(0);
     
     const formula = (num1,num2) => {
          const result = Number(num1)/Number(num2);
          if(isNaN(result)||result===Infinity||result===-Infinity){
               return 0;
          }else{
               return result;
          }
     }
     const formula2 = (num1,num2) =>{
          const result = 365/(Number(num1)/Number(num2));
          if(isNaN(result)){
               return 0;
          }else{
               return result;
          }
     }

     
     return (
          <OptionWrapper>
               <OptionHeading>  </OptionHeading>
               <h3>    ()</h3>
               <FormWrapper>
                    <NumberInput 
                         type="number" 
                         id="number1"
                         placeholder='K1'
                         onChange={(e) => setNumber1(e.target.value)}
                    />
                    <LabelForInput htmlFor='number1'></LabelForInput>
                    <NumberInput 
                         type="number" 
                         id="number2"
                         placeholder='K2'
                         onChange={(e) => setNumber2(e.target.value)}
                    />
                    <LabelForInput htmlFor='number2'>   </LabelForInput>
               </FormWrapper>
               <Result>   : <ResultNumber>{formula(number1,number2)}</ResultNumber></Result>
               <Result>    : <ResultNumber>{formula2(number1,number2)}</ResultNumber></Result>
               
               <div>
                    <OptionInfo>
                         <OptionInfoHeading></OptionInfoHeading>
                         <OptionInfoDescription>   (receivable turnover ratio)      ,         (, )   .
                         <br/><br/>
                             ,     ()           .           ,          .
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading> ()</OptionInfoHeading>
                         <OptionInfoDescription>   () =  /    
                         <br/><br/>
                                            ,   2.
                         <br/><br/>
                                ,     ,       :
                         <br/><br/>
                              = 365 /    
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading> </OptionInfoHeading>
                         <OptionInfoDescription>   ,          ,           .    ,   , ..      ,    .         . ,         ,      .</OptionInfoDescription>
                    </OptionInfo>
               </div>
          </OptionWrapper>
     )
}

export default Option3;