import React, {useState} from 'react';
import {OptionWrapper,Result,OptionInfoDescription,OptionInfoHeading,OptionInfo,ResultNumber,OptionHeading,NumberInput,FormWrapper} from '../../Styles/OptionStyles';

const Option4 = () => {
     const [number1, setNumber1] = useState(0);
     const [number2, setNumber2] = useState(1);
     const [number3, setNumber3] = useState(0);
     const [number4, setNumber4] = useState(1);
     
     const formula = (num1,num2) => {
          const result = Number(num1)/Number(num2);
          if(isNaN(result)||result===Infinity||result===-Infinity){
               return 0;
          }else{
               return result;
          }
     }
     const formula2 = (num1,num2) => {
          const result = 365/(Number(num1)/Number(num2));
          if(isNaN(result)||result===Infinity||result===-Infinity){
               return 0;
          }else{
               return result;
          }
     }

     
     return (
          <OptionWrapper>
               <OptionHeading> </OptionHeading>
               <h3>  </h3>
               <FormWrapper>
                    <NumberInput 
                         type="number" 
                         name="number1"
                         placeholder=' '
                         onChange={(e) => setNumber1(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number2"
                         placeholder='  '
                         onChange={(e) => setNumber2(e.target.value)}
                    />
               </FormWrapper>
               <Result>  : <ResultNumber>{formula(number1,number2)}</ResultNumber></Result>
               <Result>   : <ResultNumber>{formula2(number1,number2)}</ResultNumber></Result>
               <br/><br/>
               <h3></h3>
               <br/><br/>
               <FormWrapper>
                    <NumberInput 
                         type="number" 
                         name="number3"
                         placeholder=''
                         onChange={(e) => setNumber3(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number4"
                         placeholder='  '
                         onChange={(e) => setNumber4(e.target.value)}
                    />
               </FormWrapper>
               <Result>  : <ResultNumber>{formula(number3,number4)}</ResultNumber></Result>
               <Result>   : <ResultNumber>{formula2(number3,number4)}</ResultNumber></Result>
               <div>
                    <OptionInfo>
                         <OptionInfoHeading></OptionInfoHeading>
                         <OptionInfoDescription>  (inventory turnover) ,           .         ,    ,    .     ,      ""  (..   ,  ).  ,          (  )    (   ).
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading> ()</OptionInfoHeading>
                         <OptionInfoDescription>     .
                         <br/><br/>
                         1.        :
                         <br/><br/>
                           () =   /   
                         <br/><br/>
                                         2.
                         <br/><br/>
                         2.         :
                           () =  /   
                                    .  2-   ,       ,                     .                . ,               ,          .
                         <br/><br/>
                                  .     ,        .
                         <br/><br/>
                             = 365 /   
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading> </OptionInfoHeading>
                         <OptionInfoDescription>      ,                .         ,   ,     .         ,       ,        .
                         <br/><br/>
                          ,       .          ,       .</OptionInfoDescription>
                    </OptionInfo>
               </div>
          </OptionWrapper>
     )
}

export default Option4;