import React, {useState} from 'react';
import {OptionWrapper,Result,OptionInfoDescription,OptionInfoHeading,OptionInfo,ResultNumber,OptionHeading,NumberInput,FormWrapper} from '../../Styles/OptionStyles';

const Option4 = () => {
     const [number1, setNumber1] = useState(0);
     const [number2, setNumber2] = useState(0);
     const [number3, setNumber3] = useState(0);
     const [number4, setNumber4] = useState(0);
     const [number5, setNumber5] = useState(1);
     
     const formula = (num1,num2,num3,num4,num5) => {
           return ((Number(num1)-Number(num2))-(Number(num3)-Number(num4)))/Number(num5);
     }

     return (
          <OptionWrapper>
               <OptionHeading>  </OptionHeading>
               <h3>   </h3>
               <FormWrapper>
               <NumberInput 
                         type="number" 
                         name="number1"
                         placeholder=''
                         onChange={(e) => setNumber1(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number2"
                         placeholder=' '
                         onChange={(e) => setNumber2(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number3"
                         placeholder=' '
                         onChange={(e) => setNumber3(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number4"
                         placeholder='   '
                         onChange={(e) => setNumber4(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number5"
                         placeholder=''
                         onChange={(e) => setNumber5(e.target.value)}
                    />
               </FormWrapper>
               <Result>  : <ResultNumber>{formula(number1,number2,number3,number4,number5)}</ResultNumber></Result>
               <div>
                    <OptionInfo>
                         <OptionInfoHeading></OptionInfoHeading>
                         <OptionInfoDescription>
                            (asset coverage ratio)          .  ,       .          ,   .         ,   , .
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading> ()</OptionInfoHeading>
                         <OptionInfoDescription>      :
                         <br/><br/>
                         1.         .
                         <br/><br/>
                         2.        ,       ,    1.
                         <br/><br/>
                         3.   2-         (  ).
                         <br/><br/>
                          ,        :
                         <br/><br/>
                           = ((   )  (      )) / 
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading> </OptionInfoHeading>
                         <OptionInfoDescription>      ,  ,          2,    1,5.          .
                         <br/><br/>
                                   ,    .  ,  ,           ,       ,       (,  ,     ,     ).</OptionInfoDescription>
                    </OptionInfo>
               </div>
          </OptionWrapper>
     )
}

export default Option4;