import React, {useState} from 'react';
import {OptionWrapper,LabelForInput,Result,OptionInfoDescription,OptionInfoHeading,OptionInfo,ResultNumber,OptionHeading,NumberInput,FormWrapper} from '../../Styles/OptionStyles';

const Option5 = () => {
     const [number1, setNumber1] = useState(0);
     const [number2, setNumber2] = useState(0);
     const [number3, setNumber3] = useState(0);
     const [number4, setNumber4] = useState(0);

     
     const [number2_1, setNumber2_1] = useState(0);
     const [number2_2, setNumber2_2] = useState(0);
     const [number2_3, setNumber2_3] = useState(0);
     const [number2_4, setNumber2_4] = useState(0);
     const [number2_5, setNumber2_5] = useState(0);
     const [number2_6, setNumber2_6] = useState(0);
     
     const formula = (num1,num2,num3,num4) => {
          const result = 0.53*Number(num1)+0.13*Number(num2)+0.18*Number(num3)+0.16*Number(num4);
          if(isNaN(result)||result===Infinity||result===-Infinity){
               return 0;
          }else{
               return result;
          }
     }
     const formula2 = (num1,num2,num3,num4,num5,num6) => {
          const result = 0.53*(Number(num1)/Number(num2))+0.13*(Number(num3)/Number(num4))+0.18*(Number(num2)/Number(num6))+0.16*(Number(num5)/Number(num6));
          if(isNaN(result)||result===Infinity||result===-Infinity){
               return 0;
          }else{
               return result;
          }
     }

     return (
          <OptionWrapper>
               <OptionHeading>  (Z- )</OptionHeading>
               <h3>   </h3>
               <FormWrapper>
                    <NumberInput 
                         type="number" 
                         id="number1"
                         placeholder='1'
                         onChange={(e) => setNumber1(e.target.value)}
                    />
                    <LabelForInput htmlFor="number1">          </LabelForInput>
                    <NumberInput 
                         type="number" 
                         id="number2"
                         placeholder='2'
                         onChange={(e) => setNumber2(e.target.value)}
                    />
                    <LabelForInput htmlFor="number2">       </LabelForInput>
                    <NumberInput 
                         type="number" 
                         id="number3"
                         placeholder='3'
                         onChange={(e) => setNumber3(e.target.value)}
                    />
                    <LabelForInput htmlFor="number3">       </LabelForInput>
                    <NumberInput 
                         type="number" 
                         id="number4"
                         placeholder='4'
                         onChange={(e) => setNumber4(e.target.value)}
                    />
                    <LabelForInput htmlFor="number4">     </LabelForInput>
               </FormWrapper>
               <Result> : <ResultNumber>{formula(number1,number2,number3,number4)}</ResultNumber></Result>
               <br/><br/>
               <h3></h3>
               <br/><br/>

               <FormWrapper>
                    <NumberInput 
                         type="number" 
                         id="number2_1"
                         placeholder='1'
                         onChange={(e) => setNumber2_1(e.target.value)}
                    />
                    <LabelForInput htmlFor="number2_1">     </LabelForInput>
                    <NumberInput 
                         type="number" 
                         id="number2_2"
                         placeholder='2'
                         onChange={(e) => setNumber2_2(e.target.value)}
                    />
                    <LabelForInput htmlFor="number2_2">  </LabelForInput>
                    <NumberInput 
                         type="number" 
                         id="number2_3"
                         placeholder='3'
                         onChange={(e) => setNumber2_3(e.target.value)}
                    />
                    <LabelForInput htmlFor="number2_3">  </LabelForInput>
                    <NumberInput 
                         type="number" 
                         id="number2_4"
                         placeholder='4'
                         onChange={(e) => setNumber2_4(e.target.value)}
                    />
                    <LabelForInput htmlFor="number2_4">  </LabelForInput>
                    <NumberInput 
                         type="number" 
                         id="number2_5"
                         placeholder='5'
                         onChange={(e) => setNumber2_5(e.target.value)}
                    />
                    <LabelForInput htmlFor="number2_5"></LabelForInput>
                    <NumberInput 
                         type="number" 
                         id="number2_6"
                         placeholder='6'
                         onChange={(e) => setNumber2_6(e.target.value)}
                    />
                    <LabelForInput htmlFor="number2_6">  </LabelForInput>
               </FormWrapper>
               <Result> : <ResultNumber>{formula2(number2_1,number2_2,number2_3,number2_4,number2_5,number2_6)}</ResultNumber></Result>  
     

               <div>
                    <OptionInfo>
                         <OptionInfoHeading></OptionInfoHeading>
                         <OptionInfoDescription>           ,   1977    .   . .                       .
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading> ()</OptionInfoHeading>
                         <OptionInfoDescription>     :
                         <br/><br/>
                         Z = 0,531 + 0,132 + 0,183 + 0,164, 
                         <br/><br/>
                         1            ;
                         <br/><br/>
                         2         ;
                         <br/><br/>
                         3         ;
                         <br/><br/>
                         4       .
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading> </OptionInfoHeading>
                         <OptionInfoDescription>  Z   .   Z  0,3,    ,      ,   0,2     .   </OptionInfoDescription>
                    </OptionInfo>
               </div>
          </OptionWrapper>
     )
}

export default Option5;