import React, {useState} from 'react';
import {OptionWrapper,Result,OptionInfoDescription,OptionInfoHeading,OptionInfo,ResultNumber,OptionHeading,NumberInput,FormWrapper} from '../../Styles/OptionStyles';

const Option10 = () => {
     const [number1, setNumber1] = useState(0);
     const [number2, setNumber2] = useState(1);
     const [number3, setNumber3] = useState(1);
     const [number4, setNumber4] = useState(1);
     const [number5, setNumber5] = useState(0);
     const [number6, setNumber6] = useState(1);
     const [number7, setNumber7] = useState(0);
     
     const formula = (num1,num2,num3,num4) => {
          return (Number(num1)/Number(num2))*(Number(num2)/Number(num3))*(Number(num3)/Number(num4));
     }

     const formula2 = (num1,num2,num3) => {
          return Number(num1)*Number(num2)*Number(num3);
     }

     //   1 
     //  2
     //  3
     //   4

     //  
     //  
     //  

     return (
          <OptionWrapper>
               <OptionHeading> </OptionHeading>
               <h3>   </h3>
               <FormWrapper>
                    <NumberInput 
                         type="number" 
                         name="number1"
                         placeholder=' '
                         onChange={(e) => setNumber1(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number2"
                         placeholder=''
                         onChange={(e) => setNumber2(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number3"
                         placeholder=''
                         onChange={(e) => setNumber3(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number4"
                         placeholder=' '
                         onChange={(e) => setNumber4(e.target.value)}
                    />
               </FormWrapper>
               <Result>   : <ResultNumber>{formula(number1,number2,number3,number4)}</ResultNumber></Result>
               <br/><br/>
               <h3></h3>
               <br/><br/>
               <h3>    </h3>
               <FormWrapper>
                    <NumberInput 
                         type="number" 
                         name="number5"
                         placeholder=' '
                         onChange={(e) => setNumber5(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number6"
                         placeholder=''
                         onChange={(e) => setNumber6(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number7"
                         placeholder=''
                         onChange={(e) => setNumber7(e.target.value)}
                    />
               </FormWrapper>
               <Result>    : <ResultNumber>{formula2(number5,number6,number7)}</ResultNumber></Result>
               <div>
                    <OptionInfo>
                         <OptionInfoHeading></OptionInfoHeading>
                         <OptionInfoDescription>  (DuPont formula)            (ROE)     :  ,     .      DuPont  20-   ,     .
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading> ()</OptionInfoHeading>
                         <OptionInfoDescription>
                         ROE ( ) = (  / ) * ( / ) * ( /  ) =   *   *  
                         <br/><br/>
                             ,     :
                         <br/><br/>
                            (    );
                         <br/><br/>
                              (  );
                         <br/><br/>
                           ,      ( );
                         <br/><br/>
                          ,       ,    ,       .
                         </OptionInfoDescription>
                    </OptionInfo>
               </div>
          </OptionWrapper>
     )
}

export default Option10;