import React, {useState} from 'react';
import {OptionWrapper,Result,OptionInfoDescription,OptionInfoHeading,OptionInfo,ResultNumber,OptionHeading,NumberInput,FormWrapper} from '../../Styles/OptionStyles';

const Option6 = () => {
     const [number1, setNumber1] = useState(0);
     const [number2, setNumber2] = useState(0);
     
     const formula = (num1,num2) => {
          const result = Number(num1)/Number(num2);
          if(isNaN(result)){
               return 0;
          }else{
               return result;
          }
     }
     const formula2 = (num1,num2) => {
          const result = 365/(Number(num1)/Number(num2));
          if(isNaN(result)){
               return 0;
          }else{
               return result;
          }
     }

     
     return (
          <OptionWrapper>
               <OptionHeading>  </OptionHeading>
               <h3>   </h3>
               <FormWrapper>
                    <NumberInput 
                         type="number" 
                         name="number1"
                         placeholder=''
                         onChange={(e) => setNumber1(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number2"
                         placeholder=' '
                         onChange={(e) => setNumber2(e.target.value)}
                    />
               </FormWrapper>
               <Result>   : <ResultNumber>{formula(number1,number2)}</ResultNumber></Result>
               <Result>    : <ResultNumber>{formula2(number1,number2)}</ResultNumber></Result>
               <div>
                    <OptionInfo>
                         <OptionInfoHeading></OptionInfoHeading>
                         <OptionInfoDescription>   () ,            .   ,   : ,  ,       ,     .             .    ,       .
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading> ()</OptionInfoHeading>
                         <OptionInfoDescription>    :
                         <br/><br/>
                            =  /  
                         <br/><br/>
                                    ,     (..           2).
                         <br/><br/>
                                  .
                         <br/><br/>
                              = 365 /    
                         <br/><br/>
                               ,      ,     .
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading> </OptionInfoHeading>
                         <OptionInfoDescription>  ,      ,    ,     ,       .   ,    ,      (     , ).</OptionInfoDescription>
                    </OptionInfo>
               </div>
          </OptionWrapper>
     )
}

export default Option6;