import React, {useState} from 'react';
import {OptionWrapper,Result,OptionInfoDescription,OptionInfoHeading,OptionInfo,ResultNumber,OptionHeading,NumberInput,FormWrapper} from '../../Styles/OptionStyles';

const Option1 = () => {
     const [number1, setNumber1] = useState(0);
     const [number2, setNumber2] = useState(0);
     const [number3, setNumber3] = useState(0);
     const [number4, setNumber4] = useState(0);
     const [number2_1, setNumber2_1] = useState(0);
     const [number2_2, setNumber2_2] = useState(0);
     const [number2_3, setNumber2_3] = useState(0);
     const [number2_4, setNumber2_4] = useState(0);
     const [number2_5, setNumber2_5] = useState(0);
     
     const formula = (num1,num2,num3,num4) => {
           return 6.56*Number(num1)+3.26*Number(num2)+6.72*Number(num3)+1.05*Number(num4);
     }

     const formula2 = (num1,num2,num3,num4,num5) => {
          return 0.717*Number(num1)+0.847*Number(num2)+3.107*Number(num3)+0.42*Number(num4)+0.998*Number(num5);
     }

     return (
          <OptionWrapper>
               <OptionHeading>Z-  (Z- )</OptionHeading>
               <h3> 4-  Z- </h3>
               <FormWrapper>
                    <NumberInput 
                         type="number" 
                         name="number1"
                         placeholder=' '
                         onChange={(e) => setNumber1(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number2"
                         placeholder=' '
                         onChange={(e) => setNumber2(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number3"
                         placeholder='EBIT'
                         onChange={(e) => setNumber3(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number4"
                         placeholder=' '
                         onChange={(e) => setNumber4(e.target.value)}
                    />
               </FormWrapper>
               <Result>4-  Z- :  <ResultNumber>{formula(number1,number2,number3,number4)}</ResultNumber></Result>
               <br/><br/><br/><br/>
               <h3> 5- Z- </h3>
               <FormWrapper>
                    <NumberInput 
                         type="number" 
                         name="number1"
                         placeholder=' '
                         onChange={(e) => setNumber2_1(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number2"
                         placeholder=' '
                         onChange={(e) => setNumber2_2(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number3"
                         placeholder='EBIT'
                         onChange={(e) => setNumber2_3(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number4"
                         placeholder=' '
                         onChange={(e) => setNumber2_4(e.target.value)}
                    />
                    <NumberInput 
                         type="number" 
                         name="number5"
                         placeholder=''
                         onChange={(e) => setNumber2_5(e.target.value)}
                    />
               </FormWrapper>
               <Result>5- Z- :  <ResultNumber>{formula2(number2_1,number2_2,number2_3,number2_4,number2_5)}</ResultNumber></Result>
               <div>
                    <OptionInfo>
                         <OptionInfoHeading></OptionInfoHeading>
                         <OptionInfoDescription>Z-  (Z- , Altman Z-Score)     (),     ,      . 
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading> ()</OptionInfoHeading>
                         <OptionInfoDescription>     4-5   ,       .       60-   . ,          .
                         <br/><br/>
                         4-  Z-      (     ).      :
                         <br/><br/>                         
                         Z-score = 6.56T1 + 3.26T2 + 6.72T3 + 1.05T4
                         <br/><br/>
                         ,
                         <br/><br/>
                         T1 =   / <br/>
                         T2 =   / <br/>
                         T3 = EBIT / <br/>
                         T4 =   / <br/>
                         <br/><br/>
                           :
                         <br/><br/>
                         1.1    "" ,    ;<br/>
                          1.1  2.6  "" ,  ,    ,   ;<br/>
                         2.6    "" ,   <br/>
                            (     )  5- Z- .     :<br/>
                         Z-score = 0.717T1 + 0.847T2 + 3.107T3 + 0.42T4 + 0.998T5
                         <br/><br/>
                         ,
                         <br/><br/>
                         T1 =   / <br/>
                         T2 =   / <br/>
                         T3 = EBIT / <br/>
                         T4 =   / <br/>
                         T5 =  / <br/>
                         <br/><br/>
                           :
                         <br/><br/>
                         1.23    "" ,    ;<br/>
                          1.23  2.9  "" ,  ,    ,   ;<br/>
                         2.9    "" ,   <br/>
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading> </OptionInfoHeading>
                         <OptionInfoDescription>-      , ,  (),           .            .  ,     ,     ,    .
                         <br/><br/>
                         -  ,    ,      ,       (   ).                ,        .</OptionInfoDescription>
                    </OptionInfo>
               </div>
          </OptionWrapper>
     )
}

export default Option1;