import React, {useState} from 'react';
import {OptionWrapper,Result,OptionInfoDescription,OptionInfoHeading,OptionInfo,ResultNumber,OptionHeading,NumberInput,FormWrapper, LabelForInput} from '../../Styles/OptionStyles';

const Option1 = () => {
     const [number1, setNumber1] = useState(0);
     const [number2, setNumber2] = useState(0);
     const [number3, setNumber3] = useState(0);
     
     const formula = (num1,num2,num3) => {
          const result = Number(num1)+Number(num2)-Number(num3);
          if(isNaN(result)||result===Infinity||result===-Infinity){
               return 0;
          }else{
               return result;
          }
     }

     return (
          <OptionWrapper>
               <OptionHeading>  (  )</OptionHeading>
               <h3>  </h3>
               <FormWrapper>
                    <NumberInput 
                         type="number" 
                         id="number1"
                         placeholder='K1'
                         onChange={(e) => setNumber1(e.target.value)}
                    />
                    <LabelForInput htmlFor='number1'>   </LabelForInput>
                    <NumberInput 
                         type="number" 
                         id="number2"
                         placeholder='K2'
                         onChange={(e) => setNumber2(e.target.value)}
                    />
                    <LabelForInput htmlFor='number2'>    </LabelForInput>
                    <NumberInput 
                         type="number" 
                         id="number3"
                         placeholder='K3'
                         onChange={(e) => setNumber3(e.target.value)}
                    />
                    <LabelForInput htmlFor='number3'>    </LabelForInput>
               </FormWrapper>
               <Result>EBIT:  <ResultNumber>{formula(number1,number2,number3)}</ResultNumber></Result>
               <div>
                    <OptionInfo>
                         <OptionInfoHeading></OptionInfoHeading>
                         <OptionInfoDescription> ,     (cash conversion cycle, operating cycle)             (, ,  )           .            .
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading> ()</OptionInfoHeading>
                         <OptionInfoDescription>          ,   :  ,       :
                         <br/><br/>
                           ( ) =     +           
                         </OptionInfoDescription>
                    </OptionInfo>
                    <OptionInfo>
                         <OptionInfoHeading> </OptionInfoHeading>
                         <OptionInfoDescription>        ,    .   ,    .              .   ,       . ,          ,        (     )   (    ).</OptionInfoDescription>
                    </OptionInfo>
               </div>
          </OptionWrapper>
     )
}

export default Option1;